
@-webkit-keyframes down{
   form{-webkit-transform:rotateZ(0deg);transform:rotateZ(0deg)}
   to{-webkit-transform:rotateZ(180deg);transform:rotateZ(180deg)}
}

@keyframes down{
   form{-webkit-transform:rotateZ(0deg);transform:rotateZ(0deg)}
   to{-webkit-transform:rotateZ(180deg);transform:rotateZ(180deg)}
}

@-webkit-keyframes up{
    form{-webkit-transform:rotateZ(180deg);transform:rotateZ(180deg)}
    to{-webkit-transform:rotateZ(0deg);transform:rotateZ(0deg)}
 }

@keyframes up{
    form{-webkit-transform:rotateZ(180deg);transform:rotateZ(180deg)}
    to{-webkit-transform:rotateZ(0deg);transform:rotateZ(0deg)}
 }
 
 @-webkit-keyframes downDom{
    form{max-height: 0}
    to{max-height: 180}
 }
 
 @keyframes downDom{
    form{max-height: 0}
    to{max-height: 180}
 }
 
 @-webkit-keyframes upDom{
    form{max-height: 180}
    to{max-height: 0}
 }
 
 @keyframes upDom{
    form{max-height: 180}
    to{max-height: 0}
 }
 

.yss_down{
    -webkit-animation: down .3s forwards;
            animation: down .3s forwards;
}
.yss_up{
    -webkit-animation: up .3s forwards;
            animation: up .3s forwards;
}

.yss_downDom{
    -webkit-animation: downDom 1s forwards;
            animation: downDom 1s forwards;
}
.yss_upDom{
    -webkit-animation: upDom 1s forwards;
            animation: upDom 1s forwards;
}


